.current-user {
  display: flex;
  //align-items: center;
  font-family: "Source Sans Pro", sans-serif;
  overflow: hidden;

  &.mobile {
    flex-direction: column;
  }

  &__info {
    display: flex;
    flex-direction: column;
    //align-items: flex-end;
    text-align: right;
    margin-right: 11px;
    overflow: hidden;

    &.mobile {
      order: 1;
      align-items: flex-start;
      margin-top: 13px;
    }
  }

  &__name {
    color: #000000;
    font-size: 14px;
    padding-top: 5px;
    padding-right: 1px;
    overflow: hidden;
    text-overflow: ellipsis;

    &.mobile {
      font-size: 20px;
      padding-top: 0;
    }
  }

  &__email {
    color: #8d8c8c;
    font-size: 12px;
    padding-top: 4px;

    &.mobile {
      padding-top: 0;
      font-size: 13px;
      margin-top: 8px;
    }
  }

  &__photo {
    margin-top: 1px;

    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }

    &.mobile {

      img {
        width: 67px;
        height: 67px;
      }
    }
  }
}